.container{
    padding: 50px 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px;
}

.item{

    width: 215px;
    height: 300px;
    border-radius: 4px;
    box-shadow: 3px 7px 5px -3px rgba(0,0,0,0.39);
    
    background-color: #4D4C7D;
    
    font-size: 12px;
    padding: 10px 10px;
    text-align: center;
    transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    }

    .item:hover{
        box-shadow: 0px 7px 12px 0px rgba(0,0,0,0.59);
        transform: scale(1.1);
        
    }
    
    .img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
    height: 80%;
    object-fit: contain;
    }

    .link{
        color: #f5f5f5;
        font-weight: 700;
        font-size: 14px;
        overflow: hidden;
        
    }
    .link:hover{
        color: #F99417;
        
    }
    .link:focus{
        color: #F99417;
        
    }

    .title{
        display: inline-block;
        margin-top: 15px;
    }
    
    